<script>
    // @ts-nocheck
    import {onMount} from 'svelte';
    import {fade} from 'svelte/transition';
    
    let width = 600;
    let height = 600;
    let color = "#FFFFFF";
    
    const generatePoints = (n) => {
        const points = [];
        const d = (2 * Math.PI) / n;
        for (let i = 0; i < n; i++) {
            const x = width / 2 + (width / 2 - 20) * Math.cos(d * i);
            const y = height / 2 + (height / 2 - 20) * Math.sin(d * i);
            points.push([x, y]);
        }
        return points;
    };
    
    let canvas;
    let ctx;
    
    function animateLineDrawing(x1, y1, x2, y2) {    
        return new Promise((resolve, reject) => {
            
                let start = performance.now();
                let duration = 1000;
                function animate(currentTime) {
                    let progress = (currentTime - start) / duration;
                    if (progress > 1) {
                        progress = 1;
                    }
                    ctx.beginPath();
                    ctx.moveTo(x1, y1);
                    ctx.lineTo(x1 + (x2 - x1) * progress, y1 + (y2 - y1) * progress);
                    ctx.stroke();
                    if (progress < 1)
                        requestAnimationFrame(animate);
                    else
                    {
                        resolve();
                        ctx.closePath();
                    }
                }
                requestAnimationFrame(animate);
    
        })
    }
    
    const drawLines = (points) => {
        return new Promise(async (resolve, reject) => {
            try{
                ctx = canvas.getContext("2d");
                ctx.lineWidth = 2.7;
                ctx.strokeStyle = color;
                var delay = 0;
                for (let i = 0; i < points.length; i++) {
                    for (let j = i + 1; j < points.length; j++) {
                        await animateLineDrawing(points[i][0], points[i][1],points[j][0], points[j][1]);
                        if (i === points.length - 2 && j === points.length - 1) {
                            setTimeout(() => {
                                ctx.clearRect(0, 0, 600, 600);
                                resolve();
                            }, 3000)
                        }
                    }
                }                
            }
            catch(err)
            {
                reject();
            }
        })
    }
    
    const vectorEquilibrium = async (n) => {
        const points = generatePoints(n);
        try{
            await drawLines(points);
            nextStep();
        }
        catch(e)
        {}
    };
    
    let i = 6;
    let first = true;
    
    const nextStep = () => {
        if (i === 27) {
            i = 6;
        }
        if (first) {
            setTimeout(() => {
                first = false;
                setTimeout(() => {
                    nextStep();
                }, 1000)
            }, 4000);
        } else {
            vectorEquilibrium(i++);
        }
    };
    
    onMount(() => {
        nextStep();
    });
    </script>

{#if !first}
  <canvas bind:this={canvas} width="600" height="600" class="img-fluid profile-photo" in:fade={{ delay: 200 }}></canvas>
{:else}
  <div class="font-weight-500 declare" in:fade={{ delay: 100 }}>
