<script>
    export let project_name;
    export let project_description;
    export let project_videolink;
    export let project_languages;
    export let project_links;
    export let project_tags;
    const palette=[
        {"color-name":"bg-warning","color":"#ffc107"},
        {"color-name":"bg-success","color":"#198754"},
        {"color-name":"bg-info","color":"#0dcaf0"},
        {"color-name":"bg-danger","color":"#dc3545"},
        {"color-name":"bg-primary","color":"#0d6efd"},
        {"color-name":"bg-light","color":"#ffff"},
        {"color-name":"bg-secondary","color":"#6c757d"},
    ]
 
 </script>
 
 <div class="py-2">
    <div class="fs-4 my-2">{project_name}</div>
 
     <div class="row">
        <div class="col-lg-6 col-md-12 mb-2">
          <div class="ratio ratio-16x9">
             <iframe class="rounded-3" loading="lazy" src={project_videolink} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
          </div>
          <div class="mt-2">
             {#each project_tags as tag}
                <span class="px-2 border border-1 border-secondary rounded">
                   <span class="font-size-xsmall align-middle">{tag}</span>
                </span>&nbsp;
             {/each}
 
          </div>
        </div>
        <div class="col-lg-6 col-md-12">
           <p class="lh-lg text-justify">
             {project_description}
           </p>
           
          <div class="progress rounded my-1" style="height: 5px;">
             {#each project_languages as project,i}
                <div class="progress-bar {palette[i]['color-name']}" aria-label={project['language']} role="progressbar" style="width: {project['percentage']}%" aria-valuenow={project['percentage']} aria-valuemin="0" aria-valuemax={project['percentage']}></div>
             {/each}
          </div>
 
           <div class="d-flex flex-wrap my-2">
             {#each project_languages as project,i}
             <div class="my-1 me-2">
                 <small>
                    <svg fill={palette[i]['color']} width="20px" height="20px" viewBox="0 0 64 64" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xml:space="preserve" xmlns:serif="http://www.serif.com/" style="fill-rule:evenodd;clip-rule:evenodd;stroke-linejoin:round;stroke-miterlimit:2;"><g id="SVGRepo_bgCarrier" stroke-width="0"></g><g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g><g id="SVGRepo_iconCarrier"> <g transform="matrix(1,0,0,1,-576,-320)"> <rect id="Icons" x="0" y="0" width="1280" height="800" style="fill:none;"></rect> <g id="Icons1" serif:id="Icons"> <g id="Strike"> </g> <g id="H1"> </g> <g id="H2"> </g> <g id="H3"> </g> <g id="list-ul"> </g> <g id="hamburger-1"> </g> <g id="hamburger-2"> </g> <g id="list-ol"> </g> <g id="list-task"> </g> <g id="trash"> </g> <g id="vertical-menu"> </g> <g id="horizontal-menu"> </g> <g id="sidebar-2"> </g> <g id="Pen"> </g> <g id="Pen1" serif:id="Pen"> </g> <g id="clock"> </g> <g id="external-link"> </g> <g id="hr"> </g> <g id="info"> </g> <g id="warning"> </g> <g id="plus-circle"> </g> <g id="minus-circle"> </g> <g id="vue"> </g> <g id="cog"> </g> <g id="logo"> </g> <g id="radio-check"> </g> <g id="eye-slash"> </g> <g id="eye"> </g> <g id="toggle-off"> </g> <g id="shredder"> </g> <g id="spinner--loading--dots-" serif:id="spinner [loading, dots]"> </g> <g id="react"> </g> <g id="check-selected"> </g> <g id="circle-filled" transform="matrix(1.70002,0,0,1.70002,-316.778,-246.387)"> <circle cx="543.992" cy="352" r="14.13"></circle> </g> <g id="turn-off"> </g> <g id="code-block"> </g> <g id="user"> </g> <g id="coffee-bean"> </g> <g transform="matrix(0.638317,0.368532,-0.368532,0.638317,785.021,-208.975)"> <g id="coffee-beans"> <g id="coffee-bean1" serif:id="coffee-bean"> </g> </g> </g> <g id="coffee-bean-filled"> </g> <g transform="matrix(0.638317,0.368532,-0.368532,0.638317,913.062,-208.975)"> <g id="coffee-beans-filled"> <g id="coffee-bean2" serif:id="coffee-bean"> </g> </g> </g> <g id="clipboard"> </g> <g transform="matrix(1,0,0,1,128.011,1.35415)"> <g id="clipboard-paste"> </g> </g> <g id="clipboard-copy"> </g> <g id="Layer1"> </g> </g> </g> </g></svg>
                    <span class="align-bottom">{project['language']} {project['percentage']}%</span>
                 </small>
              </div>
              {/each}
           </div>
          
 
           <div class="d-flex flex-wrap my-2">
             {#each project_links as project,i}
             <div class="my-1 me-2">
                <a type="button" role="button" class="btn btn-sm btn-outline-primary rounded-pill text-white font-weight-300" href={project['link']} target="_blank" rel="noreferrer">
                   <small class="px-2">
                      {project['name']}
                   </small>
                 </a>
              </div>
              {/each}
           </div>
 
        </div>
 
     </div>
  </div>
  <hr>
 