<script>
    import {domain_list} from './skillData.js';
 </script>
 
 <div class="fs-2 text-white ms-1">Skills</div>
 <small class="fw-divghter text-white ms-1">Categorized by domains.</small>
 <div class="row">
    {#each domain_list as domain}
       <div class="col-sm-12 col-xs-12 col-lg-4 col-md-6 col-xl-4 col-xxl-4 d-flex align-items-stretch">
          <div class="card border-primary mb-3 mt-2 bg-transparent text-white w-100">
             <div class="card-header bg-transparent font-weight-500">
                {domain['domain']}
             </div>
             <div class="card-body">
                <div class="row">
                   {#each domain['children'] as skill}
                      <div class="{skill.length>=10?"col-12":"col-6"}">•&nbsp;&nbsp; {skill}</div>
                   {/each}
                </div>
             </div>
          </div>
       </div>
    {/each}
 </div>